import React from 'react';
import classes from "./classes.module.css";
import menuImg from "../News/menuImg.svg";

const listArr = ['Все темы', 'Аренда', 'Продажа', 'Ипотека', 'Аукцион', 'Льготы', 'Инвестиции', 'Переезд', 'Ремонт', '...'];

export default function Menu() {
    return <div className={classes.menu}>
        <h3 className={classes.menuTitle}>
            Прочитать
        </h3>
        <nav className={classes.nav}>
            <ul className={classes.list}>
                {
                    listArr.map((value, index) => <li className={classes.link} key={index}>
                        <a href='#'>{value}</a>
                    </li>)
                }
            </ul>
        </nav>
        <button className={classes.listButton}>
            <img src={menuImg} alt='menuImg'/>
        </button>
        <button className={classes.menuButton}>
            <img src={menuImg} alt='menuImg'/>
        </button>
    </div>
}