import React from 'react';
import classes from './classes.module.css';
import message from './message.svg';

export default function CardBig({imgUrl, text, messageCount, date, isRow}) {
    return <div className={classes.card + ' ' + (isRow ? classes.row : classes.column)}>
        <div className={classes.cardImage}>
            <img src={imgUrl} alt='news image'/>
        </div>
        <div className={classes.cardContent}>
            <p>{text}</p>
        </div>
        <div className={classes.cardFooter}>
            <span className={classes.date}>{date}</span>
            <span>
                <img src={message}/>
                <span className={classes.viewsCount}>{messageCount}</span>
            </span>
        </div>
    </div>
}